body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

td {
  white-space:nowrap;
}

.btn-primary {
  background-color: #434596 !important; 
  border-color: #434596 !important;
}

.bg-custom-primary {
  background-color: #434596 !important;
}

.bg-custom-secondary {
  background-color: #F06735 !important;
}

.btn-warning {
  background-color: #F06735 !important; 
  border-color: #F06735 !important;
}

.btn-outline-primary, .icon {
  color: #434596 !important; 
  border-color: #434596 !important;
}

.btn-outline-primary:hover {
  background-color: #434596 !important;
  color:white !important; 
}

/* Nav & SideBar */

.navbar .navbar-toggler {
  top: .25rem;
  right: 1rem;
}

.feather {
  width: 16px;
  height: 16px;
  vertical-align: text-bottom;
}

.sidebar {
  position: fixed;
  top: 0;
  /* rtl:raw:
  right: 0;
  */
  bottom: 0;
  /* rtl:remove */
  left: 0;
  z-index: 100; /* Behind the navbar */
  padding: 90px 0 0; /* Height of navbar */
  box-shadow: inset -1px 0 0 rgba(0, 0, 0, .1);
}

.sidebar-sticky {
  position: relative;
  top: 0;
  height: calc(100vh - 48px);
  padding-top: .5rem;
  overflow-x: hidden;
  overflow-y: auto; /* Scrollable contents if viewport is shorter than content. */
}

.sidebar .nav-link {
  font-weight: 500;
  color: #333;
}

.sidebar .nav-link .feather {
  margin-right: 4px;
  color: #727272;
}

.sidebar .nav-link:hover {
  color:#F06735;
}

.sidebar-heading {
  font-size: .75rem;
  text-transform: uppercase;
}

.navbar-brand {
  padding-top: .75rem;
  padding-bottom: .75rem;
  font-size: 1rem;
  background-color: rgba(0, 0, 0, .25);
  box-shadow: inset -1px 0 0 rgba(0, 0, 0, .25);
}

.img-menu {
  max-width: 120px;
}

/* Login */

.container-login {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  max-width: 400px !important;
}

a:link { 
 text-decoration:none; 
}

.img-login {
  max-width: 180px;
}

/* User */

.container-center {
  min-height: 100%;  /* Fallback for browsers do NOT support vh unit */
  min-height: 100vh; /* These two lines are counted as one :-)       */

  display: flex;
  align-items: center;
}

/* Inicial */

.center-cropped {
  object-fit: cover;
  object-position: center;
  height: 200px;
}

.card:hover {
  border-color: #F06735;
}

.icon:hover {
  color:#F06735 !important;
}

.card-link, .card-link:hover{
    color: inherit; 
    text-decoration: inherit;
}

@media (max-width: 350px) {
  .card-body {
    padding-left: 5px !important;
    padding-right: 5px !important;
  }

  .badge {
    padding-left: 5px !important;
    padding-right: 5px !important;
  }
}

/* reservation */

.value-span {
  margin-top: 6px !important;
}

.value-separator {
  margin-top: 6px !important;
  margin-bottom: 15px !important;
}

.windowLeft {
  margin: .25rem .25rem .25rem 0px !important;
}

